.CalcContainer {
  border: 0;
  margin: auto;
  padding: 0;
  border-radius: 1rem;
  width: 20rem;
  height: 30rem;
  box-shadow: 0.5rem 0.5rem 3rem #00000052;
  position: relative;
  background: inherit;
  overflow: hidden;
}

.CalcContainer:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 20rem #ffffff1c;
  filter: blur(0.5rem);
  background: inherit;
  margin: -2rem;
  z-index: 1;
}

.Display{
  outline: none;
  text-align: start;
  margin: auto;
  border: 0;
  border-radius: 0 0 1rem 1rem;
  padding: 0 1rem;
  font-size: 3rem;
  font-family: 'Open Sans Condensed', sans-serif;
  display: flex;
  align-items: center;
  width: 100%;
  height: 20%;
  background-color: #020202ab;
  color: #ffffff;
  box-sizing: border-box;
  position: absolute;
  z-index: 10;
}

.Inputs {
  border: 0;
  padding: 0.5rem;
  margin: 0;
  box-sizing: border-box;
  width: 100%;
  height: 80%;
  background-color: #ffffff0c;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  position: absolute;
  bottom: 0;
  z-index: 10;
}

.Inputs button{
  outline: none;
  border: 0;
  margin: 0.5rem;
  padding: 0;
  font-size: medium;
  border-radius: 6rem;
  background-color: #0000004f;
  color: #ffffff;
  cursor: pointer;

  transition: all 0.2s;
  transition-delay: 30ms;
}


.Inputs button:active{
  background-color: #868686;
  transition: none;
}

#Clear:active,
#Backspace:active{
  background-color: #ce5a5a;
  color: #292929;
}
  
#Enter:active,
#Operator:active{
  background-color: #61a8f8;
  color: #292929;
}

#Clear {
  grid-column: 1/3;
  grid-row: 1;
}


#Enter{
  grid-column: 3/5;
  grid-row: 5;
}


img{
  width: 28%;
}

@media(hover: hover){
  .Inputs button:hover{
    background-color: #868686;
    color: #292929;
  }
  
  #Clear:hover,
  #Backspace:hover{
    background-color: #ce5a5a;
    color: #292929;
  }
  
  #Enter:hover,
  #Operator:hover{
    background-color: #61a8f8;
    color: #292929;
  }
}

@media(max-width: 1100px){
  .CalcContainer{
    width: 24rem;
    height: 36rem
  }

  button:active{
    background-color: #ce5a5a;
    color: #292929;
    
  }

  button:hover{
    -webkit-tap-highlight-color: transparent;
  }

  #Clear:hover,
  #Backspace:hover{
    outline: none;
  }
}
 